'use strict';
function Preload() {
  this.asset = null;
  this.ready = false;
}

Preload.prototype = {
  preload: function() {
    this.asset = this.add.sprite(this.game.width/2,this.game.height/2, 'preloader');
    this.asset.anchor.setTo(0.5, 0.5);

    this.load.onLoadComplete.addOnce(this.onLoadComplete, this);
    this.load.setPreloadSprite(this.asset);
    this.load.image('yeoman', 'assets/yeoman-logo.png');
    this.load.image('hero', 'assets/tempdude.png');
    this.load.image('background', 'assets/WOF-background.png');
    this.load.image('wheelCenter', 'assets/WOF-center.png');
    this.load.image('wheelColors', 'assets/WOF-colors.png');
    this.load.image('wheelFrame', 'assets/WOF-frame.png');
    this.load.image('wheelLighting', 'assets/WOF-lighting.png');
    this.load.image('wheelStand', 'assets/WOF-stand.png');
    this.load.image('wheelNeedle', 'assets/WOF-needle.png');
    this.load.image('wheelTop', 'assets/WOF-top.png');
  },
  create: function() {
    this.asset.cropEnabled = false;
  },
  update: function() {
    if(!!this.ready) {
      this.game.state.start('play');
    }
  },
  onLoadComplete: function() {
    this.ready = true;
  }
};

module.exports = Preload;

